import { Component, OnInit } from '@angular/core';

import { Common} from '../common/common';

@Component({
  selector: 'app-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.css']
})
export class ContentComponent implements OnInit {

  public youtubeUrl;
  public videoHeight;
  public videoWidth;

  public dfVideos: any;
  constructor() {
    const common = new Common();
    this.dfVideos = common.getVideos();
    this.videoHeight =  common.videoHeight;
    this.videoWidth = common.videoWidth;
    this.youtubeUrl = common.youtubeUrl;

  }

  ngOnInit() {
  }

}
