export class Common {

 public _dfVideos: any;
 public appName: string;
 public cats: any;

 public youtubeUrl = 'https://www.youtube.com/embed/';
 public videoHeight = 400;
 public videoWidth = 800;
 public allCats: string [];

  constructor () {
     this._dfVideos = [
        { url: 'uUEdpOJ93PU', cat: 'Sales Cloud', title:   'Sales Cloud Keynote: Now Every Sales Team Gets AI' } ,
        { url: '9qiYRkDHrGw', cat: 'Service Cloud', title:   'Service Cloud Keynote: Customer Success in the New Age of Service' } ,
        { url: 'g18kXRwXnIc', cat: 'FSC', title:   'Financial Services Keynote' },
        { url: 'vE5RLPzbihU', cat: 'EA', title:   'Einstein Analytics Keynote: AI + Analytics for Every Business Process' },
        { url: 'M9R2XBLty20', cat: 'Live Recording', title:   'Dreamforce 2018 Live Broadcast - Day 3' },
        { url: 'mkMjM_S3eC0', cat: 'Live Recording', title:   'Dreamforce 2018 Live Broadcast - Day 4' },
        { url: 'Hj653eyxI34', cat: 'Marketing', title:   'Salesforce Pardot: B2B Marketing on the World #1 CRM' },
        { url: 'Mp1pZHgDpOw', cat: 'Community Cloud', title:   'Community Cloud Keynote: Win the Digital Experience Race' }




     ];

    this.appName = 'Dreamforce 18';
    this.cats = {};
    this._dfVideos.forEach(video => {
      if (this.cats[video.cat] === undefined) {
          this.cats[video.cat] = [];
      }
      this.cats[video.cat].push(video);
    });
    this.allCats = Object.keys(this.cats);
    console.log(this.allCats);

  }
 public getVideos() {
    return this._dfVideos;
  }

}
